h1, h2, h3, h4 {
    margin: 0;
}
.pageSetup {
    margin: auto;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}
/*All set position here */
.position-relative {
    position: relative;
}
.position-fixed {
    position: fixed;
}
.z-index-0 {
    z-index: 0;
}
.z-index-1 {
    z-index: 1;
}.z-index-2 {
    z-index: 2;
}
.z-index-3 {
    z-index: 3;
}
.z-index-4 {
    z-index: 4;
}
.z-index-5 {
    z-index: 5;
}
.z-index-6 {
    z-index: 6;
}
.top-0 {
    top:0;
    transition: top 0.3s linear;
}
.top-go-up-5rem {
    top: -5rem;
    transition: top 0.3s linear;
}
.top-go-up-6rem {
    top: -6rem;
    transition: top 0.3s linear;
}
.top-go-up-11rem {
    top: -11rem;
    transition: top 0.3s linear;
}
.top-12rem-height {
    height: 12rem;
    width: 100%;
    background-color: #1b2d3c;
}
.top-11rem-height {
    height: 11rem;
    width: 100%;
    background-color: #1b2d3c;
}
.display-flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
}
/*END set position here */
/*Search-tab*/
.search-tab {
    position: absolute;
    top: 50px;
    min-height: 50svh;
    width: 40svw;
    background-color: #12171be7;
    border-radius: 5px;
    padding: 0.5rem;
}
.show-search{
    opacity: 1;
    transition: all 1s linear;
}
.hide-search {
    opacity: 0;
    visibility: hidden;
    transition: all 1s linear;
}
.search-movie-item {
    width: 100%;
    height: 5rem;
    display: flex;
    cursor: pointer;
}
.search-movie-title {
    width: 60%;
    padding: 1rem;
    justify-content: flex-start;
}
.search-movie-img {
    width: 30%;
    height: auto;
    object-fit: cover;
}
/*ALL for HEADER*/
.header-container {
    width: 100%;
}
/*Header-top*/
.header-top-bg {
    width: 100%;
    height: 6rem;
    background-color: #1b2d3c;    
    display: flex;
    justify-content: center;
}
.header-top-contain {
    display: flex;
    height: 6rem;
    justify-content: center;
    align-items: center;
    color:antiquewhite;
}
/*END header-top*/
/*Header-bot - Navigation*/
.nav-container {
    width: 100%;
    display: flex;
    justify-content: center;
}
.nav-bg{
    position: absolute;
    width: 100%;
    height: 5rem;
}
.nav-text {
    display: flex;
    align-items: center;
    font-size: calc(5px + 1vw);
    line-height: 1;
    margin: auto;
    font-weight: 200;
    font-family: 'Poppins', sans-serif;
}
.nav-bg-show {
    background-color: rgba(18, 23, 28, 1);
    transition: all 1s linear;
}
.nav-bg-opacity-4 {
    background-color: rgba(18, 23, 28, 0.5);
    transition: all 1s linear;
}
.nav-bg-transparent {
    background-color: rgba(18, 23, 28, 0);
    transition: all 1s linear;
}
.nav-contain-position {
    position: relative;
    display: flex;
    color:antiquewhite;
    flex-wrap: wrap;
    height: 5rem;
}
.nav-list-contain {
    margin: 0;
    padding: 0 0rem 0 0rem;
    display: flex;
}
.nav-list {
    display: flex;
}
.nav-genres-list, .nav-genres-item, .home {
    cursor: pointer;
    background-color: rgba(18, 23, 28, 0);
}
.home {
    display: flex;
    margin-left: 1rem;
}
.nav-genres-list {
    padding: 0 0.5rem;
    height: 2rem;
    margin: auto 0;
}
.nav-genres-item {
    padding: 0 0.5rem;
    margin: auto 0;
}
.nav-genres-list, .nav-genres-list:active, .nav-genres-list:hover, .nav-genres-list:focus {
    display: flex;
    flex-wrap: wrap;
    border: solid 1px gray;
    border-radius: 5px;
    color: antiquewhite;
    outline: none;
    line-height: 100%;
    box-sizing: border-box;
}
.nav-genres-list, .nav-genres-list:hover {
    background-color: rgba(18, 23, 28, 0);
}
.nav-genres-list:focus {
    background-color: rgba(18, 23, 28, 1);
}
.nav-home-group {
    all:unset;
    display: flex;
    margin: auto;
}
.fa-house {
    font-size: calc(5px + 1.3vw);
    padding-right: 0.5rem;
}
/*END Header-bot - Navigation*/

.home-bg {
    background-color: #1b2d3c;
}
.detailPage-bg{
    background-color: #1b2d3c;
}
.container {
    display: flex;
    position: relative;
    width: 90%;
    max-width: 1500px;
    gap: 5%;
    padding: 1rem;
}
.main-container {
    display: flex;
    position: inherit;
    border-radius: 3px;
    width: 69%;
    padding: 1rem;
    text-align: center;
}
.aside-container {
    display: flex;
    position: inherit;
    border-radius: 3px;
    width: 26%;
    padding: 1rem;
    text-align: center;
}
.home-contain {
    display: flex;
    color:antiquewhite;
    flex-direction: column;
    background-color: #12171b30;
    box-shadow: 20px 20px 5px 5px rgba(0, 0, 0, 0.2);
}
.detail-contain{
    display: flex;
    color:antiquewhite;
    background-color: #12171b30;
    margin: 2rem 0;
    box-shadow: 20px 20px 5px 5px rgba(0, 0, 0, 0.2);
    flex-wrap: wrap;
}

.detail-1 {
    position: relative;
    width: 30%;
    height: 20rem;
    border-radius: 5px;
}
.detail-3 {
    border-top: 2px gray solid;
    width: 100%;
    padding-top: 2rem;
}
.detail-2 {
    width: 68%;
    text-align: left;
    margin-left: 2%;
    height: 25rem;
    border: 1px rgb(176, 141, 141) solid;
    border-radius: 5px;
    padding: 1rem;
    display: flex;
    flex-direction: column;
}
.info-text {
    line-height: 1;
    margin: 0;
    padding: 0;
    border-bottom: #3f474e 1px solid;
    height: 3rem;
    display: flex;
    align-items: center;
}
.logo {
    flex: 1 0 2rem;
    display: flex;
    min-width: 2rem;
    align-items: center;
    justify-content: center;
}
.logo img {
    cursor: pointer;
    width: auto;
    height: 50px;
}
.search-contain {
    position: relative;
    flex: 2 0 2rem;
}
.searchBar{
    position: relative;
    margin: 1rem 0.1rem;
    width: 100%;
    height: 2rem;
    cursor: text;
    text-align: center;
    border-radius: 50px;
    box-shadow: inset 2px 2px 5px #727272, inset -2px -2px 5px #ccc;
    padding: 10px;
    border: none;
    background-color: #cecece;
    outline: none;
}
.searchBar:focus {
    outline: none;
    background-color: #e0e0e0;
}
.search-icon {
    all: unset;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    color: #12171b;
}
.user-icon {
    flex: 1 0 2rem;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    gap: 1rem;
}

.footer {
    display: flex;
    height: 70px;
    border-top: 1px solid gray;
    box-shadow: 1px 0px 0px 0px gray;
    justify-content: center;
    background-color: #12171b;
}
.footerText{
    display: flex;
    align-items: center;
    color: wheat;
}

.buttonContain{
    position: relative;
    display: flex;
    justify-content: center;
    width: 100%;
    max-width: 1300px;
    padding: 0 0.2rem;
    bottom: 0;
    opacity: 1;
    background-color: #1b2d3c;
    gap: 20%
}
.buttonPages {
    position: relative;
    display: flex;
    justify-content: center;
    opacity: 1;
}
.button-special{
    width: 30% !important;
}
.button{
    position: relative;
    margin: 0.5rem 0.1rem;
    width: 3rem;
    height: 3rem;
    background-color: #12171b ;
    cursor: pointer;
    border: #20272ba4 1px solid;
   color: antiquewhite;
   border-radius: 5px;
   box-shadow: 1px 1px 1px 1px #253036a4 inset;
}
.pageInput{
    position: relative;
    margin: 0.5rem 0.5rem;
    width: 3rem;
    height: 3rem;
    cursor: text;
    text-align: center;
    border-radius: 5px;
    box-shadow: inset 2px 2px 5px #727272, inset -2px -2px 5px #ccc;
    padding: 10px;
    border: none;
    background-color: #cecece;
}
.pageInput:focus{
    box-shadow: inset 2px 2px 5px #727272, inset -2px -2px 5px #ccc;
    padding: 10px;
    border: none;
    background-color: #e0e0e0;
    outline: none;
}
.active {
    background-color: orange;
}
.hidden {
    display: none;
}
.group {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.main-title {
    display: flex;
    position: relative;
}
.line {
    position: relative;
    width: 100%;
    height: 1px;
    margin: 0.2rem 0 1rem 0;
    background-color: aquamarine;
}
.black-line {
    width: 100%;
    height: 1px;
    margin: 0.2rem 0 1rem 0;
    background-color: rgba(255, 255, 255, 0.292);
    position: absolute;
    bottom: 0;
}
.genresItem {
    cursor: pointer;
}
.movie-grid {
    display: grid;
    grid-template: auto/ repeat(4, 1fr);
    grid-gap: 1rem;
    padding: 0;
    margin-bottom: 1rem;
}
.movieItem {
    position: relative;
    border-radius: 5px;
    cursor: pointer;
    aspect-ratio: 1 / 1;
    overflow: visible;
}
.movie-title {
    border-radius: inherit;
    position: absolute;
    font-size: calc(5px + 0.6vw);
    min-height: 3rem;
    padding: 0.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    background-color: hsla(207, 38%, 17%, 0.4);
    bottom: 0rem;
    color: rgba(240, 248, 255, 0.785);
    transition: color 0.2s linear, background-color 0.2s linear;
}
.movie-bg {
    border-radius: inherit;
    position: relative;
    height: 100%;
}
.movie-img {
    border-radius: inherit;
    position: relative;
    bottom: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all 0.5s linear;
}
.bigger {
    transform: scale(1.3);
    transition: all 0.5s linear;
}
.bigger2 {
    color: white;
    background-color: hsla(207, 38%, 17%, 0.8);
}
.aside-movie-grid {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    background-color: #12171b;

}
.aside-movieItem {
    border-radius: inherit;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-bottom: 1px solid gray;
}
.aside-movie-title {
    border-radius: inherit;
    width: 50%;
    padding: 1rem;
}
.movie-title-detail {
    min-height: 5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
.aside-movie-bg{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: inherit;
    width: 50%;
}
.focus {
    opacity: 1;
    color: green;
    background-color:aliceblue;
    transition: all 0.5s linear 0.2s;
}
.visible {
    opacity: 1;
    transition: all 0.5s linear 0.2s;
}
.invisible {
    opacity: 0;
    transition: all 0.5s linear 0.2s;
}
.main {
    min-height: 80svh;
}

/* Movie detail container*/
.detailPage-container {
    position: relative;
}
.movie-detail-container {
    width: 100%;
    height: 100svh;
    position: relative;
    background-color: rgba(18, 23, 28, 0);
}
.movie-container {
    width: 100%;
    height: 100%;
    text-align: center;
    font-size: 18px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}
.movie-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.movie-detail-fore-ground {
    background-color: transparent;
    height: 100%;
    width: 100%;
    position: absolute;
    opacity: 0.5;
    box-shadow: inset 300px 0px 1000px 10px #000000;
    pointer-events: none;
}
.movie-detail-background-gradient {
    position: absolute;
    height: 200px;
    width: 100%;
    background: linear-gradient(to top, rgba(27, 45, 60, 1), rgba(27, 45, 60, 0));
    bottom: 0;
}
.movie-detail-group {
    position: absolute;
    left: 5rem;
    bottom: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 25.5rem;
    padding: 1rem;
}
.movie-detail-title {
    position: relative;
    color: white;
    opacity: 0.6;
    border-radius: 5px;
    font-size: 2.8rem;
    max-width: 50rem;
    font-family: 'Rubik Doodle Triangles', system-ui;
    line-height: 1;
    margin-bottom: 2rem;
    text-align: left;
}
.movie-detail-buttons {
    display: flex;
    justify-content: space-between;
    position: relative;
    margin-bottom: 1rem;
    width: 100%;
    min-width: 22rem;
    gap: 5px;
}
.movie-detail-button1,
.movie-detail-button2 {
    width: auto;
    height: 2.5rem;
    border-radius: 50px;
    outline: none;
    border: gray solid 1px;
    padding: 0 2rem;
    font-family: 'Poppins', sans-serif;
    display: flex;
    align-items: center;
}
.movie-detail-button-icon {
    margin-right: 5px;
    font-size: 1.5rem;
}
.movie-detail-overview {
    display: flex;
}
.movie-detail-overview-text {
    font-family: 'Mali', cursive;
    text-align: left;
    font-size: 1rem;
    color: whitesmoke;
    mix-blend-mode: difference;
    width: 100%;
}
.movie-detail-view-detail {
    cursor: pointer;
    padding-left: 0.5rem;
    color: gray;
}
.movie-detail-view-icon {
    font-size: 1.5rem;
    position: relative;
    top: 4px;
    font-weight: 600;
}
@media screen and (max-width: 500px) {
    .pageSetup{
        max-width: 500px;
        margin: auto;
    }
    h1 {
        font-size: 1.5rem;
    }
    .container {
        padding: 0.5rem;
        gap: 5px;
    }
    .movie-grid {
        grid-gap: 0.3rem;
        grid-template: auto/ repeat(3, 1fr);
    }
    .main-container{
        width: 100%;
        padding: 0.5rem;
    }
    .aside-container {
        display: none;
    }
    .nav-genres-list, .nav-genres-item, .home {
        cursor: pointer;
        margin: 0.5rem 0.5rem 0.5rem 0rem;
    }
    .movie-img{
        height: 15rem;
        border-radius: 5px;
    }
    .detail-1 {
        height: 20rem;
        width: 100%;
        margin-bottom: 1rem;
    }
    .detail-2 {
        height: 20rem;
        margin-bottom: 1rem;
        width: 100%;
        margin-left: 0;
    }
    .detail-2 {

    }
}